// src/lib/auth/login.ts
import { jwtDecode } from "jwt-decode";

// src/lib/auth/auth0.ts
import {
  createAuth0Client
} from "@auth0/auth0-spa-js";
var getAuth0Client = () => createAuth0Client({
  domain: "auth.othent.io",
  clientId: "uXkRmJoIa0NfzYgYEDAgj6Rss4wR1tIc",
  authorizationParams: {
    redirect_uri: window.location.origin
  }
});
function getTokenSilently(auth0, authParams) {
  return auth0.getTokenSilently({
    detailedResponse: true,
    authorizationParams: authParams,
    cacheMode: "off"
  });
}

// src/lib/auth/login.ts
async function login() {
  const auth0 = await getAuth0Client();
  const isAuthenticated = await auth0.isAuthenticated();
  const baseOptions = {
    authorizationParams: {
      transaction_input: JSON.stringify({
        othentFunction: "KMS"
      }),
      redirect_uri: window.location.origin
    }
  };
  const loginAndGetDecodedJWT = async (options) => {
    await auth0.loginWithPopup(options);
    const authParams = {
      transaction_input: JSON.stringify({ othentFunction: "KMS" })
    };
    const accessToken = await getTokenSilently(auth0, authParams);
    const jwtObj = jwtDecode(accessToken.id_token);
    localStorage.setItem("id_token", accessToken.id_token);
    return { encoded: accessToken.id_token, decoded: jwtObj };
  };
  const processDecodedJWT = async (decoded_JWT) => {
    const fieldsToDelete = [
      "nonce",
      "sid",
      "aud",
      "iss",
      "iat",
      "exp",
      "updated_at"
    ];
    fieldsToDelete.forEach(
      (field) => delete decoded_JWT[field]
    );
    return decoded_JWT;
  };
  if (isAuthenticated) {
    const { decoded } = await loginAndGetDecodedJWT(baseOptions);
    return processDecodedJWT(decoded);
  } else {
    try {
      const { decoded } = await loginAndGetDecodedJWT(baseOptions);
      return processDecodedJWT(decoded);
    } catch (error) {
      throw new Error(`${error}`);
    }
  }
}

// src/lib/operations/api.ts
import axios from "axios";
var api = axios.create({
  baseURL: "https://kms-server.othent.io"
});

// src/lib/auth/encodeToken.ts
async function encodeToken(data) {
  const auth0 = await getAuth0Client();
  const authParams = {
    transaction_input: JSON.stringify({ othentFunction: "KMS", data })
  };
  const accessToken = await getTokenSilently(auth0, authParams);
  const JWT = accessToken.id_token;
  return JWT;
}

// src/lib/operations/createUser.ts
async function createUser() {
  const encodedData = await encodeToken({ data: null });
  try {
    const createUserRequest = (await api.post("/create-user", { encodedData })).data.data;
    if (!createUserRequest) {
      throw new Error("Error creating user on server.");
    }
    return createUserRequest;
  } catch (e) {
    throw new Error("Error creating user on server.");
  }
}

// src/lib/auth/getJWT.ts
async function getJWT() {
  const auth0 = await getAuth0Client();
  const authParams = {
    transaction_input: JSON.stringify({ othentFunction: "KMS" })
  };
  const accessToken = await getTokenSilently(auth0, authParams);
  const JWT = accessToken.id_token;
  return JWT;
}

// src/lib/auth/userDetails.ts
import { jwtDecode as jwtDecode2 } from "jwt-decode";
async function userDetails() {
  const id_token = localStorage.getItem("id_token");
  if (!id_token) {
    throw new Error("Error retrieving session id_token.");
  }
  const decoded_JWT = jwtDecode2(id_token);
  delete decoded_JWT.nonce;
  delete decoded_JWT.sid;
  delete decoded_JWT.aud;
  delete decoded_JWT.iss;
  delete decoded_JWT.iat;
  delete decoded_JWT.exp;
  delete decoded_JWT.updated_at;
  return decoded_JWT;
}

// src/lib/mapping/connect.ts
async function connect() {
  const user = await login();
  if (user.authSystem === "KMS" && user.owner && user.walletAddress) {
    return user;
  } else {
    await createUser();
    const userDetailsJWT = await getJWT();
    localStorage.setItem("id_token", JSON.stringify(userDetailsJWT));
    return await userDetails();
  }
}

// src/lib/operations/decrypt.ts
async function decrypt(ciphertext, keyName) {
  const encodedData = await encodeToken({ ciphertext, keyName });
  try {
    const decryptRequest = (await api.post("/decrypt", { encodedData })).data.data;
    if (!decryptRequest) {
      throw new Error("Error decrypting on server.");
    }
    return decryptRequest;
  } catch (e) {
    throw new Error("Error decrypting on server.");
  }
}

// src/lib/mapping/decrypt.ts
async function decrypt2(ciphertext) {
  const user = await userDetails();
  const decryptedData = await decrypt(ciphertext, user.sub);
  return decryptedData;
}

// src/lib/auth/logOut.ts
async function logOut() {
  const auth0 = await getAuth0Client();
  await auth0.logout({
    logoutParams: {
      returnTo: window.location.origin
    }
  });
  return { response: "User logged out" };
}

// src/lib/mapping/disconnect.ts
async function disconnect() {
  await logOut();
  return null;
}

